import React from 'react';
import { createPortal } from 'react-dom';

import styles from './Backdrop.module.scss';

type Props = {
  onHide: () => void;
};

const Backdrop = ({ onHide }: Props) => {
  return createPortal(
    <div
      className={[styles.backdrop, styles.show].join(' ')}
      onClick={onHide}
    ></div>,
    document.body
  );
};

export default Backdrop;
