import React, {
  FunctionComponent,
  Component,
  useState,
  useCallback,
  Fragment,
} from 'react';
import { Link } from 'gatsby';
import { useStaticQuery, graphql } from 'gatsby';
import { cloneDeep } from 'lodash';

import styles from './index.module.scss';

import Header from '../components/header/header';
import Layout from '../components/layout/Layout';
import SEO from '../components/seo';

import Section from '../components/section/Section';
import Photo from '../components/photo/Photo';
import Biography from '../components/biography/Biography';
import VideoTile from '../components/video/Video';
import AudioTile from '../components/audio/Audio';
import Recommendations from '../components/recommendations/Recommendations';
import Quote from '../components/quote/Quote';
import Reviews from '../components/reviews/Reviews';
import Gallery from '../components/gallery/Gallery';
import Contact from '../components/contact/Contact';

import Modal from '../components/modal/Modal';
import AudioPlayer from '../components/player/Player';

type Props = {
  data: any;
};

type State = {
  activeIndex: number;
  lastActiveIndex: number;
  videoModal: {
    isShown: boolean;
    videoId: null | string;
  };
  audioPlayer: {
    name: string;
    file: string;
  } | null;
};

class IndexPage extends Component<Props, State> {
  blocks: any[];
  mounted: boolean;

  constructor(props: any) {
    super(props);
    this.state = {
      activeIndex: 0,
      lastActiveIndex: 0,
      videoModal: {
        isShown: false,
        videoId: null,
      },
      audioPlayer: null,
    };
    this.blocks = [];
    this.mounted = false;
  }

  componentDidMount() {
    const { content } = this.props.data;

    this.mounted = true;
    let activeIndex = 0;
    let lastActiveIndex = 0;

    for (let i = 0; i < this.blocks.length; i++) {
      activeIndex = i;

      if (
        window.scrollY <
        this.blocks[i].current.offsetTop +
          this.blocks[i].current.offsetHeight -
          window.innerHeight / 2
      ) {
        break;
      }
    }

    for (let j = 0; j < this.blocks.length; j++) {
      lastActiveIndex = j;

      if (
        window.scrollY <
        this.blocks[j].current.offsetTop +
          this.blocks[j].current.offsetHeight -
          window.innerHeight * 0.7
      ) {
        break;
      }
    }

    if (activeIndex !== this.state.activeIndex) {
      this.setState({ activeIndex });
    }

    if (lastActiveIndex > this.state.lastActiveIndex) {
      this.setState({ lastActiveIndex });
    }

    window.addEventListener('scroll', (e) => {
      for (let i = 0; i < this.blocks.length; i++) {
        activeIndex = i;

        if (
          window.scrollY <
          this.blocks[i].current.offsetTop +
            this.blocks[i].current.offsetHeight -
            window.innerHeight / 2
        ) {
          break;
        }
      }

      for (let j = 0; j < this.blocks.length; j++) {
        lastActiveIndex = j;

        if (
          window.scrollY <
          this.blocks[j].current.offsetTop +
            this.blocks[j].current.offsetHeight -
            window.innerHeight * 0.7
        ) {
          break;
        }
      }

      if (activeIndex !== this.state.activeIndex) {
        this.setState({ activeIndex });
      }

      if (lastActiveIndex > this.state.lastActiveIndex) {
        this.setState({ lastActiveIndex });
      }
    });
  }

  getRef = (ref: any, i: any) => {
    this.blocks[i] = ref;
  };

  render() {
    const { activeIndex, lastActiveIndex, audioPlayer } = this.state;

    const {
      site,
      content,
      videos,
      audios,
      galleryPhotos,
      galleryThumbnails,
    } = this.props.data;

    return (
      <>
        <Header
          siteTitle={site.siteMetadata.title}
          activeIndex={activeIndex}
          activeSlug={this.blocks[activeIndex]?.current.id}
        />
        <Layout>
          <SEO title="Home" />
          {content.edges.map(({ node }: any, i: any) => {
            if (node.frontmatter.slug === 'photo') {
              return (
                <Section
                  key={node.id}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                  animation="fadeIn"
                >
                  <Photo
                    src={node.frontmatter.file.publicURL}
                    position={node.frontmatter.position}
                  />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'biography') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <Biography content={node.html} />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'recommendation') {
              return (
                <Section
                  key={node.id}
                  id={`${node.frontmatter.slug}${i}`}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <Quote content={node.html} source={node.frontmatter.source} />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'review') {
              return (
                <Section
                  key={node.id}
                  id={`${node.frontmatter.slug}${i}`}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <Quote
                    title={node.frontmatter.title}
                    content={node.html}
                    source={node.frontmatter.source}
                  />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'videos') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <div
                    className={['container-fluid', styles.container].join(' ')}
                  >
                    <div
                      className={['row no-gutters', styles.videosRow].join(' ')}
                    >
                      {videos.edges.map(({ node }: any) => {
                        return (
                          <div
                            key={node.id}
                            className={['col-lg-4', styles.videoCol].join(' ')}
                          >
                            <VideoTile
                              title={node.frontmatter.title}
                              videoId={node.frontmatter.videoId}
                              coverUrl={node.frontmatter.cover.publicURL}
                              onClick={(e) => {
                                e.preventDefault();
                                const newState = cloneDeep(this.state);
                                newState.videoModal.videoId =
                                  node.frontmatter.videoId;
                                newState.videoModal.isShown = true;
                                this.setState(newState);
                              }}
                            />
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </Section>
              );
            }

            if (node.frontmatter.slug === 'audios') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <div className={styles.audios}>
                    <div className="container-lg">
                      <div className="row justify-content-center">
                        <div className="col-lg-10">
                          <hr className={styles.divider} />
                          <div className={['row', styles.audiosRow].join(' ')}>
                            {audios.edges.map(({ node }: any) => {
                              return (
                                <div
                                  key={node.id}
                                  className={[
                                    styles.audioCol,
                                    'col-6 col-md-4',
                                  ].join(' ')}
                                >
                                  <AudioTile
                                    name={node.frontmatter.title}
                                    file={node.frontmatter.file.publicURL}
                                    onPlay={(name, file) => {
                                      const audioPlayer = {
                                        name,
                                        file,
                                      };
                                      this.setState({
                                        audioPlayer,
                                      });
                                    }}
                                    isPlaying={
                                      node.frontmatter.title ===
                                      audioPlayer?.name
                                    }
                                  />
                                </div>
                              );
                            })}
                          </div>
                          <hr className={styles.divider} />
                        </div>
                      </div>
                    </div>
                  </div>
                </Section>
              );
            }

            if (node.frontmatter.slug === 'gallery') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                >
                  <Gallery
                    photos={galleryPhotos.edges}
                    thumbnails={galleryThumbnails.edges}
                  />
                </Section>
              );
            }

            if (node.frontmatter.slug === 'contact') {
              return (
                <Section
                  key={node.id}
                  id={node.frontmatter.slug}
                  index={i}
                  getRef={this.getRef}
                  shown={i <= lastActiveIndex}
                  animation="fadeIn"
                >
                  <Contact />
                </Section>
              );
            }
          })}
        </Layout>
        {audioPlayer && (
          <AudioPlayer
            name={audioPlayer.name}
            file={audioPlayer.file}
            onClose={() => {
              this.setState({
                audioPlayer: null,
              });
            }}
          />
        )}
        {this.state.videoModal.isShown && (
          <Modal
            onHide={() => {
              const newVideoModal = cloneDeep(this.state.videoModal);
              newVideoModal.isShown = false;
              this.setState({
                videoModal: newVideoModal,
              });
            }}
          >
            <div className="embed-responsive embed-responsive-16by9">
              <iframe
                className="embed-responsive-item"
                src={`https://www.youtube.com/embed/${this.state.videoModal.videoId}`}
                allowFullScreen
              ></iframe>
            </div>
          </Modal>
        )}
      </>
    );
  }
}

export default (() => {
  const data = useStaticQuery(graphql`
    query IndexMarkdown {
      site {
        siteMetadata {
          title
        }
      }

      content: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//home//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            id
            fileAbsolutePath
            frontmatter {
              slug
              title
              source
              position
              file {
                publicURL
              }
            }
            html
          }
        }
      }

      videos: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//videos//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            id
            fileAbsolutePath
            frontmatter {
              title
              cover {
                publicURL
              }
              videoId
            }
          }
        }
      }

      audios: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//audios//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            id
            fileAbsolutePath
            frontmatter {
              title
              file {
                publicURL
              }
            }
          }
        }
      }

      galleryPhotos: allFile(
        filter: { absolutePath: { regex: "//gallery//" } }
      ) {
        edges {
          node {
            publicURL
          }
        }
      }

      galleryThumbnails: allFile(
        filter: { absolutePath: { regex: "//gallery//" } }
      ) {
        edges {
          node {
            childImageSharp {
              fixed(width: 400) {
                ...GatsbyImageSharpFixed
              }
            }
          }
        }
      }
    }
  `);

  return <IndexPage data={data} />;
}) as FunctionComponent;
