import React, { FunctionComponent } from 'react';
import { createPortal } from 'react-dom';

import styles from './GalleryModalBackdrop.module.scss';

type Props = {
  onHide: () => void;
};

const GalleryModalBackdrop: FunctionComponent<Props> = ({ onHide }) => {
  return createPortal(
    <div
      className={[styles.backdrop, styles.show].join(' ')}
      onClick={onHide}
    ></div>,
    document.body
  );
};

export default GalleryModalBackdrop;
